#forms {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding: 5px 15px;
  border: 0 none;
  border-radius: 5px;
}
#submit2 {
  cursor: pointer;
  font-size: 50px;
  width: 20vw;
  max-width: 25vw;
  height: 20vh;
  max-height: 30vw;
  border: 4px darkgrey solid;
  border-radius: 15px;
  color: black;
  background: url("https://i.pinimg.com/originals/73/f0/7a/73f07a3b375055159210d24ffd44e9ec.jpg");
}
.container {
  padding: 0px;
}
.container > img {
  -webkit-box-shadow: 5px 5px 5px black;
          box-shadow: 5px 5px 5px black;
  padding: 0px;
}
.content {
  display: grid;
  text-align: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.content > img {
  width: 500px;
  height: 400px;
  border: 5px rgb(46, 46, 46) dotted;
  /* -o-box-shadow:      12px 12px 29px #555;
    -icab-box-shadow:   12px 12px 29px #555;
    -khtml-box-shadow:  12px 12px 29px #555;
    -moz-box-shadow:    12px 12px 29px #555;
    -webkit-box-shadow: 12px 12px 29px #555; */
  -webkit-box-shadow: 800px 1200px #555;
          box-shadow: 800px 1200px #555;
}
#navigation > a {
  text-decoration: none;
  color: black;
}
#navigation a:hover {
  display: flexbox;
  color: grey;
  -webkit-transition: 500ms;
  transition: 500ms;
  -ms-flex-line-pack: justify;
      align-content: space-between;
}
#navigation {
  font-size: 5vw;
  text-decoration: none;
}



























































